"use client";

import React, { useEffect, useRef, useState } from "react";
import ReactMarkdown from "react-markdown";
import { useTranscript } from "@/app/voice/contexts/TranscriptContext";

interface TranscriptProps {
  userText: string;
  setUserText: (val: string) => void;
  onSendMessage: () => void;
  canSend: boolean;
  downloadRecording: () => void;
}

export function Transcript({
  userText,
  setUserText,
  onSendMessage,
  canSend,
  downloadRecording,
}: TranscriptProps) {
  const { transcriptItems, toggleTranscriptItemExpand } = useTranscript();
  const transcriptRef = useRef<HTMLDivElement | null>(null);
  const [previousItemsLength, setPreviousItemsLength] = useState(0);
  const [justCopied, setJustCopied] = useState(false);
  const inputRef = useRef<HTMLInputElement | null>(null);

  useEffect(() => {
    if (!transcriptRef.current) return;
    if (transcriptItems.length !== previousItemsLength) {
      transcriptRef.current.scrollTop = transcriptRef.current.scrollHeight;
      setPreviousItemsLength(transcriptItems.length);
    }
  }, [transcriptItems, previousItemsLength]);

  useEffect(() => {
    if (canSend && inputRef.current) {
      inputRef.current.focus();
    }
  }, [canSend]);

  const handleCopyTranscript = async () => {
    if (!transcriptRef.current) return;
    try {
      await navigator.clipboard.writeText(transcriptRef.current.innerText);
      setJustCopied(true);
      setTimeout(() => setJustCopied(false), 1500);
    } catch (error) {
      console.error("Failed to copy transcript", error);
    }
  };

  return (
    <div className="flex flex-col flex-1 bg-white min-h-0 rounded-xl">
      <div className="flex items-center justify-between px-6 py-3 sticky top-0 z-10 border-b bg-white rounded-t-xl">
        <span className="font-semibold">Transcript</span>
        <div className="flex gap-x-2">
          <button
            onClick={handleCopyTranscript}
            className="text-sm px-3 py-1 rounded-md bg-gray-200 hover:bg-gray-300"
          >
            {justCopied ? "Copied!" : "Copy"}
          </button>
          <button
            onClick={downloadRecording}
            className="text-sm px-3 py-1 rounded-md bg-gray-200 hover:bg-gray-300"
          >
            Download Audio
          </button>
        </div>
      </div>

      <div
        ref={transcriptRef}
        className="overflow-auto p-4 flex flex-col gap-y-4 h-full"
      >
        {[...transcriptItems]
          .sort((a, b) => a.createdAtMs - b.createdAtMs)
          .map((item) => {
            if (item.isHidden) return null;

            if (item.type === "MESSAGE") {
              const isUser = item.role === "user";
              const containerClass = `flex flex-col ${
                isUser ? "items-end" : "items-start"
              }`;
              const bubbleClass = `max-w-lg p-3 rounded-2xl ${
                isUser
                  ? "bg-gray-900 text-white rounded-br-none"
                  : "bg-gray-100 text-gray-900 rounded-bl-none"
              }`;

              const content = item.title ?? "";
              const isBracketed = content.startsWith("[") && content.endsWith("]");
              const display = isBracketed ? content.slice(1, -1) : content;

              return (
                <div key={item.itemId} className={containerClass}>
                  <div className={bubbleClass}>
                    <div className="text-xs text-gray-400 font-mono mb-1">
                      {item.timestamp}
                    </div>
                    <div className={`whitespace-pre-wrap ${isBracketed ? "italic text-gray-300" : ""}`}>
                      <ReactMarkdown>{display}</ReactMarkdown>
                    </div>
                  </div>
                </div>
              );
            }

            if (item.type === "BREADCRUMB") {
              return (
                <div
                  key={item.itemId}
                  className="flex flex-col text-sm text-gray-600"
                >
                  <span className="text-xs font-mono text-gray-400">
                    {item.timestamp}
                  </span>
                  <div
                    className={`whitespace-pre-wrap font-mono cursor-pointer ${
                      item.data ? "flex items-center" : ""
                    }`}
                    onClick={() => item.data && toggleTranscriptItemExpand(item.itemId)}
                  >
                    {item.data && (
                      <span
                        className={`mr-2 transition-transform duration-200 ${
                          item.expanded ? "rotate-90" : "rotate-0"
                        }`}
                      >
                        ▶
                      </span>
                    )}
                    {item.title}
                  </div>
                  {item.expanded && item.data && (
                    <pre className="border-l-2 border-gray-200 ml-1 pl-2 text-xs text-gray-800 whitespace-pre-wrap">
                      {JSON.stringify(item.data, null, 2)}
                    </pre>
                  )}
                </div>
              );
            }

            return (
              <div key={item.itemId} className="text-gray-500 text-xs">
                Unknown item type
              </div>
            );
          })}
      </div>

      <div className="p-4 flex items-center gap-x-2 border-t border-gray-200">
        <input
          ref={inputRef}
          type="text"
          value={userText}
          onChange={(e) => setUserText(e.target.value)}
          onKeyDown={(e) => {
            if (e.key === "Enter" && canSend) {
              onSendMessage();
            }
          }}
          placeholder="Type a message..."
          className="flex-1 px-4 py-2 border rounded-lg focus:outline-none"
        />
        <button
          onClick={onSendMessage}
          disabled={!canSend || !userText.trim()}
          className="bg-gray-900 text-white px-4 py-2 rounded-lg disabled:opacity-50"
        >
          Send
        </button>
      </div>
    </div>
  );
}
