"use client";

import React, {
  createContext,
  useContext,
  useState,
  PropsWithChildren,
} from "react";
import { v4 as uuidv4 } from "uuid";
import { TranscriptItem } from "@/app/voice/types";

type TranscriptContextValue = {
  transcriptItems: TranscriptItem[];
  addTranscriptMessage: (
    itemId: string,
    role: "user" | "assistant",
    text: string,
    isHidden?: boolean,
  ) => void;
  updateTranscriptMessage: (itemId: string, text: string, isDelta: boolean) => void;
  addTranscriptBreadcrumb: (title: string, data?: Record<string, unknown>) => void;
  toggleTranscriptItemExpand: (itemId: string) => void;
};

const TranscriptContext = createContext<TranscriptContextValue | undefined>(
  undefined,
);

export function TranscriptProvider({ children }: PropsWithChildren) {
  const [transcriptItems, setTranscriptItems] = useState<TranscriptItem[]>([]);

  function newTimestampPretty(): string {
    const now = new Date();
    const time = now.toLocaleTimeString([], {
      hour12: false,
      hour: "2-digit",
      minute: "2-digit",
      second: "2-digit",
    });
    const ms = now.getMilliseconds().toString().padStart(3, "0");
    return `${time}.${ms}`;
  }

  const addTranscriptMessage: TranscriptContextValue["addTranscriptMessage"] = (
    itemId,
    role,
    text = "",
    isHidden = false,
  ) => {
    setTranscriptItems((prev) => {
      if (prev.some((item) => item.itemId === itemId)) {
        return prev;
      }

      const newItem: TranscriptItem = {
        itemId,
        type: "MESSAGE",
        role,
        title: text,
        data: undefined,
        expanded: false,
        timestamp: newTimestampPretty(),
        createdAtMs: Date.now(),
        status: "IN_PROGRESS",
        isHidden,
      };

      return [...prev, newItem];
    });
  };

  const updateTranscriptMessage: TranscriptContextValue["updateTranscriptMessage"] = (
    itemId,
    newText,
    append,
  ) => {
    setTranscriptItems((prev) =>
      prev.map((item) =>
        item.itemId === itemId && item.type === "MESSAGE"
          ? {
              ...item,
              title: append ? `${item.title ?? ""}${newText}` : newText,
              status: "DONE",
            }
          : item,
      ),
    );
  };

  const addTranscriptBreadcrumb: TranscriptContextValue["addTranscriptBreadcrumb"] = (
    title,
    data,
  ) => {
    setTranscriptItems((prev) => [
      ...prev,
      {
        itemId: `breadcrumb-${uuidv4()}`,
        type: "BREADCRUMB",
        title,
        data,
        expanded: false,
        timestamp: newTimestampPretty(),
        createdAtMs: Date.now(),
        status: "DONE",
        isHidden: false,
      },
    ]);
  };

  const toggleTranscriptItemExpand: TranscriptContextValue["toggleTranscriptItemExpand"] = (
    itemId,
  ) => {
    setTranscriptItems((prev) =>
      prev.map((item) =>
        item.itemId === itemId
          ? { ...item, expanded: !item.expanded }
          : item,
      ),
    );
  };

  return (
    <TranscriptContext.Provider
      value={{
        transcriptItems,
        addTranscriptMessage,
        updateTranscriptMessage,
        addTranscriptBreadcrumb,
        toggleTranscriptItemExpand,
      }}
    >
      {children}
    </TranscriptContext.Provider>
  );
}

export function useTranscript() {
  const ctx = useContext(TranscriptContext);
  if (!ctx) {
    throw new Error("useTranscript must be used within a TranscriptProvider");
  }
  return ctx;
}
