"use client";

import React, { createContext, useContext, useState, PropsWithChildren } from "react";
import { v4 as uuidv4 } from "uuid";
import { LoggedEvent } from "@/app/voice/types";

type EventContextValue = {
  loggedEvents: LoggedEvent[];
  logClientEvent: (eventObj: Record<string, unknown>, eventNameSuffix?: string) => void;
  logServerEvent: (eventObj: Record<string, unknown>, eventNameSuffix?: string) => void;
  toggleExpand: (id: string) => void;
};

const EventContext = createContext<EventContextValue | undefined>(undefined);

export function EventProvider({ children }: PropsWithChildren) {
  const [loggedEvents, setLoggedEvents] = useState<LoggedEvent[]>([]);

  function addLoggedEvent(direction: "client" | "server", name: string, data: Record<string, unknown>) {
    const id = data.event_id || uuidv4();
    setLoggedEvents((prev) => [
      ...prev,
      {
        id,
        direction,
        eventName: name,
        eventData: data,
        timestamp: new Date().toLocaleTimeString(),
        expanded: false,
      },
    ]);
  }

  const logClientEvent: EventContextValue["logClientEvent"] = (eventObj, suffix = "") => {
    const name = `${eventObj.type || ""} ${suffix}`.trim();
    addLoggedEvent("client", name, eventObj);
  };

  const logServerEvent: EventContextValue["logServerEvent"] = (eventObj, suffix = "") => {
    const name = `${eventObj.type || ""} ${suffix}`.trim();
    addLoggedEvent("server", name, eventObj);
  };

  const toggleExpand: EventContextValue["toggleExpand"] = (id) => {
    setLoggedEvents((prev) =>
      prev.map((item) =>
        item.id === id ? { ...item, expanded: !item.expanded } : item,
      ),
    );
  };

  return (
    <EventContext.Provider value={{ loggedEvents, logClientEvent, logServerEvent, toggleExpand }}>
      {children}
    </EventContext.Provider>
  );
}

export function useEvent() {
  const ctx = useContext(EventContext);
  if (!ctx) {
    throw new Error("useEvent must be used within an EventProvider");
  }
  return ctx;
}
