import { useCallback, useRef, useState } from "react";
import {
    RealtimeSession,
    RealtimeAgent,
    OpenAIRealtimeWebRTC,
} from "@openai/agents/realtime";

import {
    audioFormatForCodec,
    applyCodecPreferences,
} from "@/app/voice/lib/codecUtils";
import { resolveRealtimeModel } from "@/app/voice/lib/realtimeModel";
import { useEvent } from "@/app/voice/contexts/EventContext";
import {
    useHandleSessionHistory,
    HistoryMessage,
    TransportEvent,
} from "@/app/voice/hooks/useHandleSessionHistory";
import { SessionStatus } from "@/app/voice/types";

export interface RealtimeSessionCallbacks {
    onConnectionChange?: (status: SessionStatus) => void;
}

interface ConnectOptions {
    getEphemeralKey: () => Promise<string>;
    agent: RealtimeAgent;
    audioElement?: HTMLAudioElement;
    extraContext?: Record<string, unknown>;
    outputGuardrails?: unknown[];
}

export function useRealtimeSession(callbacks: RealtimeSessionCallbacks = {}) {
    const sessionRef = useRef<RealtimeSession | null>(null);
    const [status, setStatus] = useState<SessionStatus>("DISCONNECTED");
    const { logClientEvent, logServerEvent } = useEvent();
    const historyHandlersRef = useHandleSessionHistory();
    const realtimeModel = resolveRealtimeModel(
        process.env.NEXT_PUBLIC_REALTIME_MODEL,
    );

    const updateStatus = useCallback(
        (next: SessionStatus) => {
            setStatus(next);
            callbacks.onConnectionChange?.(next);
            logClientEvent({ type: "status_change", value: next });
        },
        [callbacks, logClientEvent],
    );

    const connect = useCallback(
        async ({
            getEphemeralKey,
            agent,
            audioElement,
            extraContext,
            outputGuardrails,
        }: ConnectOptions) => {
            if (sessionRef.current) return;

            updateStatus("CONNECTING");
            const key = await getEphemeralKey();

            const codec =
                typeof window !== "undefined"
                    ? (new URLSearchParams(window.location.search).get(
                          "codec",
                      ) ?? "opus")
                    : "opus";
            const codecParam = codec.toLowerCase();
            const audioFormat = audioFormatForCodec(codecParam);

            const session = new RealtimeSession(agent, {
                transport: new OpenAIRealtimeWebRTC({
                    audioElement,
                    changePeerConnection: async (pc: RTCPeerConnection) => {
                        applyCodecPreferences(pc, codecParam);
                        return pc;
                    },
                }),
                model: realtimeModel,
                config: {
                    inputAudioFormat: audioFormat,
                    outputAudioFormat: audioFormat,
                    inputAudioTranscription: {
                        model: "gpt-4o-mini-transcribe",
                    },
                },
                outputGuardrails: outputGuardrails ?? [],
                context: extraContext ?? {},
            });

            session.on("connected", () =>
                logServerEvent({ type: "connected" }),
            );
            session.on("disconnected", () =>
                logServerEvent({ type: "disconnected" }),
            );
            session.on("error", (err) => {
                logServerEvent({ type: "error", error: err });
            });

            session.on("history_added", (item: HistoryMessage) => {
                historyHandlersRef.current.handleHistoryAdded(item);
            });
            session.on("history_updated", (items: HistoryMessage[]) => {
                historyHandlersRef.current.handleHistoryUpdated(items);
            });
            session.on("agent_output_created", (event: TransportEvent) => {
                historyHandlersRef.current.handleTransportEvent(event);
            });
            session.on("transport_event", (event: TransportEvent) => {
                switch (event.type) {
                    case "conversation.item.input_audio_transcription.delta":
                        historyHandlersRef.current.handleTranscriptionDelta(
                            event,
                        );
                        break;
                    case "conversation.item.input_audio_transcription.completed":
                    case "response.audio_transcript.done":
                        historyHandlersRef.current.handleTranscriptionCompleted(
                            event,
                        );
                        break;
                    default:
                        historyHandlersRef.current.handleTransportEvent(event);
                }
            });

            sessionRef.current = session;

            try {
                await session.connect({
                    apiKey: key,
                    model: realtimeModel,
                });
                updateStatus("CONNECTED");
            } catch (error) {
                logServerEvent({
                    type: "connection_error",
                    message:
                        error instanceof Error ? error.message : String(error),
                });
                session.close();
                sessionRef.current = null;
                updateStatus("DISCONNECTED");
                throw error;
            }
        },
        [updateStatus, historyHandlersRef, logServerEvent, realtimeModel],
    );

    const disconnect = useCallback(() => {
        sessionRef.current?.close();
        sessionRef.current = null;
        updateStatus("DISCONNECTED");
    }, [updateStatus]);

    const interrupt = useCallback(() => {
        sessionRef.current?.interrupt();
    }, []);

    const sendUserText = useCallback((text: string) => {
        if (!sessionRef.current) throw new Error("Session not connected");
        sessionRef.current.sendMessage(text);
    }, []);

    const sendEvent = useCallback((event: Record<string, unknown>) => {
        sessionRef.current?.transport.sendEvent(event);
    }, []);

    const mute = useCallback((shouldMute: boolean) => {
        sessionRef.current?.mute(shouldMute);
    }, []);

    return {
        status,
        connect,
        disconnect,
        sendUserText,
        sendEvent,
        mute,
        interrupt,
    } as const;
}
