"use client";

import React, {
    useEffect,
    useMemo,
    useRef,
    useState,
    useCallback,
} from "react";
import { v4 as uuidv4 } from "uuid";
import { useTranscript } from "@/app/voice/contexts/TranscriptContext";
import { useEvent } from "@/app/voice/contexts/EventContext";
import { useRealtimeSession } from "@/app/voice/hooks/useRealtimeSession";
import useAudioDownload from "@/app/voice/hooks/useAudioDownload";
import { Transcript } from "@/app/voice/components/Transcript";
import { Events } from "@/app/voice/components/Events";
import { BottomToolbar } from "@/app/voice/components/BottomToolbar";
import { RealtimeAgent } from "@openai/agents/realtime";

const DEFAULT_VOICE = process.env.NEXT_PUBLIC_REALTIME_VOICE ?? "verse";

const VOICE_AGENT = new RealtimeAgent({
    name: "voice_assistant",
    voice: DEFAULT_VOICE,
    instructions:
        "You are a friendly interview coach. Speak conversationally, keep responses concise, and help candidates practice describing their experience.",
});

export function VoiceApp() {
    const { addTranscriptMessage, addTranscriptBreadcrumb } = useTranscript();
    const { logClientEvent, logServerEvent } = useEvent();

    const audioElementRef = useRef<HTMLAudioElement | null>(null);

    const sdkAudioElement = useMemo(() => {
        if (typeof window === "undefined") return undefined;
        const el = document.createElement("audio");
        el.autoplay = true;
        el.style.display = "none";
        document.body.appendChild(el);
        return el;
    }, []);

    useEffect(() => {
        if (sdkAudioElement && !audioElementRef.current) {
            audioElementRef.current = sdkAudioElement;
        }
    }, [sdkAudioElement]);

    const {
        status: sessionStatus,
        connect,
        disconnect,
        sendUserText,
        sendEvent,
        mute,
        interrupt,
    } = useRealtimeSession();

    const [userText, setUserText] = useState("");
    const [isEventsPaneExpanded, setIsEventsPaneExpanded] = useState(true);
    const [isPTTActive, setIsPTTActive] = useState(false);
    const [isPTTUserSpeaking, setIsPTTUserSpeaking] = useState(false);
    const [isAudioPlaybackEnabled, setIsAudioPlaybackEnabled] = useState(() => {
        if (typeof window === "undefined") return true;
        const stored = window.localStorage.getItem("voice-audio-playback");
        return stored ? stored === "true" : true;
    });

    const { startRecording, stopRecording, downloadRecording } =
        useAudioDownload();

    useEffect(() => {
        if (typeof window === "undefined") return;
        const storedPTT = window.localStorage.getItem("voice-ptt-active");
        if (storedPTT) setIsPTTActive(storedPTT === "true");
        const storedLogs = window.localStorage.getItem("voice-logs-open");
        if (storedLogs) setIsEventsPaneExpanded(storedLogs === "true");
    }, []);

    useEffect(() => {
        if (typeof window === "undefined") return;
        window.localStorage.setItem("voice-ptt-active", String(isPTTActive));
    }, [isPTTActive]);

    useEffect(() => {
        if (typeof window === "undefined") return;
        window.localStorage.setItem(
            "voice-logs-open",
            String(isEventsPaneExpanded),
        );
    }, [isEventsPaneExpanded]);

    useEffect(() => {
        if (typeof window === "undefined") return;
        window.localStorage.setItem(
            "voice-audio-playback",
            String(isAudioPlaybackEnabled),
        );
    }, [isAudioPlaybackEnabled]);

    useEffect(() => {
        const el = audioElementRef.current;
        if (!el) return;
        if (isAudioPlaybackEnabled) {
            el.muted = false;
            el.play().catch((err) => {
                console.warn("Autoplay blocked", err);
            });
        } else {
            el.muted = true;
            el.pause();
        }
        mute(!isAudioPlaybackEnabled);
    }, [isAudioPlaybackEnabled, mute]);

    useEffect(() => {
        if (
            sessionStatus === "CONNECTED" &&
            audioElementRef.current?.srcObject
        ) {
            const remoteStream = audioElementRef.current
                .srcObject as MediaStream;
            startRecording(remoteStream);
            return () => {
                stopRecording();
            };
        }
        return undefined;
    }, [sessionStatus, startRecording, stopRecording]);

    const fetchEphemeralKey = useCallback(async () => {
        logClientEvent({ url: "/api/session" }, "fetch_session_token_request");
        const response = await fetch("/api/session", { method: "POST" });
        const data = await response.json();
        logServerEvent(data, "fetch_session_token_response");

        if (!response.ok) {
            const message =
                typeof data?.error === "string"
                    ? data.error
                    : "Failed to create session";
            throw new Error(message);
        }

        const key = data?.client_secret?.value;
        if (!key) {
            throw new Error("No ephemeral key returned");
        }
        return key as string;
    }, [logClientEvent, logServerEvent]);

    const sendClientEvent = useCallback(
        (eventObj: Record<string, unknown>, suffix = "") => {
            try {
                sendEvent(eventObj);
                logClientEvent(eventObj, suffix);
            } catch (err) {
                console.error("Failed to send event", err);
            }
        },
        [sendEvent, logClientEvent],
    );

    const sendSimulatedUserMessage = useCallback(
        (text: string) => {
            const id = uuidv4().slice(0, 32);
            addTranscriptMessage(id, "user", text, true);
            sendClientEvent(
                {
                    type: "conversation.item.create",
                    item: {
                        id,
                        type: "message",
                        role: "user",
                        content: [{ type: "input_text", text }],
                    },
                },
                "(simulated user text message)",
            );
            sendClientEvent({ type: "response.create" }, "(auto greet)");
        },
        [addTranscriptMessage, sendClientEvent],
    );

    const updateSession = useCallback(
        (shouldPrompt = false) => {
            const turnDetection = isPTTActive
                ? null
                : {
                      type: "server_vad",
                      threshold: 0.9,
                      prefix_padding_ms: 300,
                      silence_duration_ms: 500,
                      create_response: true,
                  };

            sendClientEvent({
                type: "session.update",
                session: {
                    turn_detection: turnDetection,
                },
            });

            if (shouldPrompt) {
                sendSimulatedUserMessage("hi");
            }
        },
        [isPTTActive, sendClientEvent, sendSimulatedUserMessage],
    );

    const connectToRealtime = useCallback(async () => {
        if (sessionStatus !== "DISCONNECTED") return;
        try {
            const EPHEMERAL_KEY = await fetchEphemeralKey();
            if (!EPHEMERAL_KEY) {
                console.error("Failed to fetch ephemeral key");
                return;
            }
            console.log(EPHEMERAL_KEY);
            await connect({
                getEphemeralKey: async () => EPHEMERAL_KEY,
                agent: VOICE_AGENT,
                audioElement: sdkAudioElement,
                extraContext: {
                    addTranscriptBreadcrumb,
                },
            });
            updateSession(true);
        } catch (err) {
            console.error("Error connecting to realtime session", err);
        }
    }, [
        sessionStatus,
        connect,
        fetchEphemeralKey,
        sdkAudioElement,
        addTranscriptBreadcrumb,
        updateSession,
    ]);

    const disconnectFromRealtime = useCallback(() => {
        disconnect();
        setIsPTTUserSpeaking(false);
    }, [disconnect]);

    useEffect(() => {
        if (sessionStatus === "CONNECTED") {
            updateSession(false);
        }
    }, [sessionStatus, updateSession, isPTTActive]);

    const handleSendTextMessage = useCallback(() => {
        if (!userText.trim()) return;
        interrupt();
        try {
            sendUserText(userText.trim());
            logClientEvent({ type: "user_message", text: userText.trim() });
        } catch (err) {
            console.error("Failed to send user text", err);
        }
        setUserText("");
    }, [userText, interrupt, sendUserText, logClientEvent]);

    const handleTalkButtonDown = useCallback(() => {
        if (sessionStatus !== "CONNECTED") return;
        interrupt();
        setIsPTTUserSpeaking(true);
        sendClientEvent(
            { type: "input_audio_buffer.clear" },
            "clear PTT buffer",
        );
    }, [sessionStatus, interrupt, sendClientEvent]);

    const handleTalkButtonUp = useCallback(() => {
        if (sessionStatus !== "CONNECTED" || !isPTTUserSpeaking) return;
        setIsPTTUserSpeaking(false);
        sendClientEvent({ type: "input_audio_buffer.commit" }, "commit PTT");
        sendClientEvent({ type: "response.create" }, "trigger response PTT");
    }, [sessionStatus, isPTTUserSpeaking, sendClientEvent]);

    const handleToggleConnection = useCallback(() => {
        if (sessionStatus === "CONNECTED" || sessionStatus === "CONNECTING") {
            disconnectFromRealtime();
        } else {
            connectToRealtime();
        }
    }, [sessionStatus, disconnectFromRealtime, connectToRealtime]);

    return (
        <div className="flex flex-col h-screen bg-gray-100 text-gray-800">
            <div className="px-5 py-4 text-lg font-semibold border-b bg-white flex items-center justify-between">
                <span>Realtime Voice Agent</span>
                <span className="text-sm text-gray-500">
                    Status: {sessionStatus}
                </span>
            </div>
            <div className="flex flex-1 gap-2 px-2 py-2 overflow-hidden">
                <Transcript
                    userText={userText}
                    setUserText={setUserText}
                    onSendMessage={handleSendTextMessage}
                    canSend={sessionStatus === "CONNECTED"}
                    downloadRecording={downloadRecording}
                />
                <Events isExpanded={isEventsPaneExpanded} />
            </div>
            <BottomToolbar
                sessionStatus={sessionStatus}
                onToggleConnection={handleToggleConnection}
                isPTTActive={isPTTActive}
                setIsPTTActive={setIsPTTActive}
                isPTTUserSpeaking={isPTTUserSpeaking}
                handleTalkButtonDown={handleTalkButtonDown}
                handleTalkButtonUp={handleTalkButtonUp}
                isEventsPaneExpanded={isEventsPaneExpanded}
                setIsEventsPaneExpanded={setIsEventsPaneExpanded}
                isAudioPlaybackEnabled={isAudioPlaybackEnabled}
                setIsAudioPlaybackEnabled={setIsAudioPlaybackEnabled}
            />
        </div>
    );
}

export default VoiceApp;
