"use client";

import { useRef } from "react";
import { useTranscript } from "@/app/voice/contexts/TranscriptContext";
import { useEvent } from "@/app/voice/contexts/EventContext";

export interface HistoryContentItem {
  type?: string;
  text?: string;
  transcript?: string;
}

export interface HistoryMessage {
  type?: string;
  itemId?: string;
  role?: "user" | "assistant";
  content?: HistoryContentItem[];
}

export interface TranscriptionEvent {
  item_id?: string;
  delta?: string;
  transcript?: string;
}

export interface TransportEvent {
  type?: string;
  [key: string]: unknown;
}

export function useHandleSessionHistory() {
  const {
    addTranscriptBreadcrumb,
    addTranscriptMessage,
    updateTranscriptMessage,
  } = useTranscript();
  const { logServerEvent } = useEvent();

  const extractMessageText = (content: HistoryContentItem[] = []): string => {
    return content
      .map((item) => {
        if (!item) return "";
        if (item.type === "input_text" || item.type === "output_text") {
          return item.text ?? "";
        }
        if (item.type === "audio") {
          return item.transcript ?? "";
        }
        return "";
      })
      .filter(Boolean)
      .join("\n");
  };

  function handleHistoryAdded(item: HistoryMessage) {
    if (!item || item.type !== "message") return;
    const { itemId, role, content = [] } = item;
    if (!itemId || !role) return;

    const text = extractMessageText(content) || (role === "user" ? "[Listening...]" : "");
    addTranscriptMessage(itemId, role, text);
  }

  function handleHistoryUpdated(items: HistoryMessage[]) {
    items.forEach((item) => {
      if (!item || item.type !== "message") return;
      const { itemId, content = [] } = item;
      if (!itemId) return;

      const text = extractMessageText(content);
      if (text) {
        updateTranscriptMessage(itemId, text, false);
      }
    });
  }

  function handleTranscriptionDelta(event: TranscriptionEvent) {
    const itemId = event.item_id;
    const deltaText = event.delta || "";
    if (itemId && deltaText) {
      updateTranscriptMessage(itemId, deltaText, true);
    }
  }

  function handleTranscriptionCompleted(event: TranscriptionEvent) {
    const itemId = event.item_id;
    const finalTranscript = event.transcript || "";
    if (itemId) {
      updateTranscriptMessage(itemId, finalTranscript || "[inaudible]", false);
    }
  }

  function handleTransportEvent(event: TransportEvent) {
    logServerEvent(event);
  }

  const handlersRef = useRef({
    handleHistoryAdded,
    handleHistoryUpdated,
    handleTranscriptionDelta,
    handleTranscriptionCompleted,
    handleTransportEvent,
    addTranscriptBreadcrumb,
  });

  return handlersRef;
}
