import React from "react";
import { SessionStatus } from "@/app/voice/types";

interface BottomToolbarProps {
  sessionStatus: SessionStatus;
  onToggleConnection: () => void;
  isPTTActive: boolean;
  setIsPTTActive: (value: boolean) => void;
  isPTTUserSpeaking: boolean;
  handleTalkButtonDown: () => void;
  handleTalkButtonUp: () => void;
  isEventsPaneExpanded: boolean;
  setIsEventsPaneExpanded: (value: boolean) => void;
  isAudioPlaybackEnabled: boolean;
  setIsAudioPlaybackEnabled: (value: boolean) => void;
}

export function BottomToolbar({
  sessionStatus,
  onToggleConnection,
  isPTTActive,
  setIsPTTActive,
  isPTTUserSpeaking,
  handleTalkButtonDown,
  handleTalkButtonUp,
  isEventsPaneExpanded,
  setIsEventsPaneExpanded,
  isAudioPlaybackEnabled,
  setIsAudioPlaybackEnabled,
}: BottomToolbarProps) {
  const isConnected = sessionStatus === "CONNECTED";
  const isConnecting = sessionStatus === "CONNECTING";

  const connectionLabel = isConnected
    ? "Disconnect"
    : isConnecting
      ? "Connecting..."
      : "Connect";

  return (
    <div className="p-4 flex flex-row items-center justify-center gap-6 bg-white border-t">
      <button
        onClick={onToggleConnection}
        disabled={isConnecting}
        className={`px-6 py-2 rounded-md text-white ${
          isConnected ? "bg-red-600 hover:bg-red-700" : "bg-black hover:bg-gray-900"
        } ${isConnecting ? "cursor-not-allowed opacity-70" : ""}`}
      >
        {connectionLabel}
      </button>

      <label className="flex items-center gap-2">
        <input
          type="checkbox"
          checked={isPTTActive}
          onChange={(e) => setIsPTTActive(e.target.checked)}
          disabled={!isConnected}
        />
        Push to Talk
      </label>
      <button
        onMouseDown={handleTalkButtonDown}
        onMouseUp={handleTalkButtonUp}
        onTouchStart={handleTalkButtonDown}
        onTouchEnd={handleTalkButtonUp}
        disabled={!isPTTActive}
        className={`px-4 py-2 rounded-md ${
          isPTTActive ? "bg-gray-200 hover:bg-gray-300" : "bg-gray-100 text-gray-500"
        } ${isPTTUserSpeaking ? "ring-2 ring-gray-400" : ""}`}
      >
        Talk
      </button>

      <label className="flex items-center gap-2">
        <input
          type="checkbox"
          checked={isAudioPlaybackEnabled}
          onChange={(e) => setIsAudioPlaybackEnabled(e.target.checked)}
          disabled={!isConnected}
        />
        Audio Playback
      </label>

      <label className="flex items-center gap-2">
        <input
          type="checkbox"
          checked={isEventsPaneExpanded}
          onChange={(e) => setIsEventsPaneExpanded(e.target.checked)}
        />
        Show Logs
      </label>
    </div>
  );
}
