type SenderWithCapabilities = typeof RTCRtpSender & {
  getCapabilities?: (kind: "audio") => RTCRtpCapabilities | undefined;
};

export function audioFormatForCodec(codec: string): "pcm16" | "g711_ulaw" | "g711_alaw" {
  const lower = codec.toLowerCase();
  if (lower === "pcmu") return "g711_ulaw";
  if (lower === "pcma") return "g711_alaw";
  return "pcm16";
}

export function applyCodecPreferences(
  pc: RTCPeerConnection,
  codec: string,
): void {
  try {
    const sender = RTCRtpSender as SenderWithCapabilities;
    const capabilities = sender.getCapabilities?.("audio");
    if (!capabilities) return;

    const target = capabilities.codecs.find(
      (c) => c.mimeType.toLowerCase() === `audio/${codec.toLowerCase()}`,
    );
    if (!target) return;

    pc
      .getTransceivers()
      .filter((transceiver) => transceiver.sender?.track?.kind === "audio")
      .forEach((transceiver) => transceiver.setCodecPreferences([target]));
  } catch (err) {
    console.error("Failed to apply codec preferences", err);
  }
}
