"use client";

import React, { useEffect, useRef, useState } from "react";
import { useEvent } from "@/app/voice/contexts/EventContext";

interface EventsProps {
  isExpanded: boolean;
}

export function Events({ isExpanded }: EventsProps) {
  const { loggedEvents, toggleExpand } = useEvent();
  const containerRef = useRef<HTMLDivElement | null>(null);
  const [previousCount, setPreviousCount] = useState(0);

  useEffect(() => {
    if (!containerRef.current) return;
    const hasNew = loggedEvents.length > previousCount;
    if (isExpanded && hasNew) {
      containerRef.current.scrollTop = containerRef.current.scrollHeight;
    }
    setPreviousCount(loggedEvents.length);
  }, [loggedEvents, isExpanded, previousCount]);

  const arrowForDirection = (direction: string) => {
    switch (direction) {
      case "client":
        return { symbol: "▲", color: "#7f5af0" };
      case "server":
        return { symbol: "▼", color: "#2cb67d" };
      default:
        return { symbol: "•", color: "#999" };
    }
  };

  return (
    <div
      ref={containerRef}
      className={`${
        isExpanded ? "w-1/2 opacity-100" : "w-0 opacity-0"
      } transition-all duration-200 ease-in-out bg-white rounded-xl overflow-hidden`}
    >
      {isExpanded && (
        <div className="flex flex-col h-full">
          <div className="px-6 py-3 border-b font-semibold">Logs</div>
          <div className="flex-1 overflow-auto">
            {loggedEvents.map((event) => {
              const arrow = arrowForDirection(event.direction);
              const isError =
                event.eventName.toLowerCase().includes("error") ||
                Boolean(event.eventData?.response?.status_details?.error);

              return (
                <div
                  key={event.id}
                  className="border-b border-gray-200 px-6 py-2 text-sm font-mono"
                >
                  <div
                    className="flex items-center justify-between cursor-pointer"
                    onClick={() => toggleExpand(event.id)}
                  >
                    <div className="flex items-center gap-2 flex-1">
                      <span style={{ color: arrow.color }}>{arrow.symbol}</span>
                      <span className={isError ? "text-red-600" : "text-gray-800"}>
                        {event.eventName || "(unnamed)"}
                      </span>
                    </div>
                    <span className="text-xs text-gray-500">
                      {event.timestamp}
                    </span>
                  </div>
                  {event.expanded && (
                    <pre className="text-xs text-gray-800 whitespace-pre-wrap break-words border-l-2 border-gray-200 mt-2 pl-2">
                      {JSON.stringify(event.eventData, null, 2)}
                    </pre>
                  )}
                </div>
              );
            })}
          </div>
        </div>
      )}
    </div>
  );
}
